.form {
  margin: 50px auto;
}

.input {
  display: flex;
  margin: 10px auto;
  width: 70%;
  padding: 32px 24px 28px 24px;
  font-size: 24px;
  border: transparent;
  border-radius: 10px;
  outline: none;
}

.btn {
  margin: 0 auto;
  display: block;
  color: #fffefe;  
  width: 70%;
  height: 50px;
  border: solid 1px transparent;
  font-size: 16px;
  background: #e62b28;
  border-radius: 10px;
  font-weight: 500;
  transition: linear .5s;
}

.btn:hover {
  cursor: pointer;
  background-color: transparent;
  border: solid 1px #e62b28;
  color: #e62b28;
}